<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="合同号" prop="contract_num">
                    <el-input v-model.trim="filters.contract_num" placeholder="请输入合同号"></el-input>
                </el-form-item>
                <el-form-item label="贷款人手机号" prop="contact_way">
                    <el-input v-model.trim="filters.contact_way" placeholder="请输入贷款人手机号"></el-input>
                </el-form-item>
                <el-form-item label="贷款人" prop="borrower">
                    <el-input v-model.trim="filters.borrower" placeholder="请输入贷款人"></el-input>
                </el-form-item>
                <!-- <el-form-item label="银行放款日" prop="bank_loan_start">
                    <el-date-picker
                        v-model="filters.bank_loan_start"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="银行每月扣款日" prop="bank_loan_end">
                    <el-date-picker
                        v-model="filters.bank_loan_end"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="二网放款日" prop="ew_loan_start">
                    <el-date-picker
                        v-model="filters.ew_loan_start"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="二网每月还款日" prop="ew_loan_start">
                    <el-date-picker
                        v-model="filters.ew_loan_end"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="到期还款状态" prop="is_give">
                    <el-radio-group v-model="filters.is_give">
                        <el-radio-button label="1">全部</el-radio-button>
                        <el-radio-button label="2">已还</el-radio-button>
                        <el-radio-button label="3">未还</el-radio-button>
                    </el-radio-group>
                </el-form-item>
                
                <el-button @click="resetFilters">重置</el-button>
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="handleAdd">新建</el-button>
            </el-form>
            <el-table :data="tableData" border class="mt10" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="expand" fixed="left">
                    <template slot-scope="{row}">
                        <el-table :data="row.payment" border height="250">
                            <el-table-column prop="period_number" label="期数" width="120"></el-table-column>
                            <el-table-column prop="payment_number" label="每月还款额（元）" width="120"></el-table-column>
                            <el-table-column prop="payment_day_num" label="账户还款日余额（元）" width="150"></el-table-column>
                            <el-table-column prop="search_date" label="账户余额查询日期" width="120"></el-table-column>
                            <el-table-column prop="pay_balance" label="余额是否充足" width="130"></el-table-column>
                            <el-table-column prop="payment_status" label="还款状态" width="130"></el-table-column>
                            <el-table-column prop="no_payment_day" label="未还款天数"></el-table-column>
                        </el-table>
                    </template>
                </el-table-column>
                <el-table-column prop="contract_num" label="合同号"></el-table-column>
                <el-table-column prop="sort" label="序号"></el-table-column>
                <el-table-column prop="borrower" label="贷款人"></el-table-column>
                <el-table-column prop="bank_card_number" label="卡号"></el-table-column>
                <el-table-column prop="open_bank" label="开户行"></el-table-column>
                <el-table-column prop="bank_loan_amount" label="贷款金额（元）" width="130"></el-table-column>
                <el-table-column prop="loan_date" label="放款日"></el-table-column>
                <el-table-column prop="payment_number" label="每月还款额（元）" width="140"></el-table-column>
                <el-table-column prop="payment_moth" label="每月还款日" width="100"></el-table-column>
                <el-table-column prop="account_balance" label="账户余额是否充足"></el-table-column>
                <el-table-column label="是否已还">
                    <template slot-scope="{row}">
                        <span v-if="row.is_give == 2">已还</span>
                        <span v-else-if="row.is_give == 3">未还</span>
                    </template>
                </el-table-column>
                <el-table-column prop="contact_way" label="联系电话" width="110"></el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <template v-if="row.is_give == 3">
                            <el-button type="text" icon="el-icon-view" @click="handleSetGive(row,2)">标为已还</el-button>
                        </template>
                        <template v-if="row.is_give == 2">
                            <el-button type="text" icon="el-icon-view" @click="handleSetGive(row,3)">标为未还</el-button>
                        </template>
                        <template v-if="row.is_give == 1">
                            <el-button type="text" icon="el-icon-view" @click="handleSetGive(row,3)">标为未还</el-button>
                            <el-button type="text" icon="el-icon-view" @click="handleSetGive(row,2)">标为已还</el-button>
                        </template>
                        
                        <el-button type="text" icon="el-icon-view" @click="$router.push({path: '/creditHelpManagement/view', query: {id:row.id, credit_type: 1}})">查看详情</el-button>
                        <el-button type="text" icon="el-icon-view" @click="handleHandleLogs(row, $index)">操作记录</el-button>
                        <el-button type="text" icon="el-icon-view" @click="handleSetBlack(row)">记为黑名单</el-button>
                        <el-button type="text" icon="el-icon-view" @click="handleEdit(row)">编辑卡折信息</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>

        <!-- 选择客户 -->
        <el-dialog title="选择客户" :visible.sync="dialogSelectClient" width="50%">
            <selectClient v-if="hackReset" :selectClientOptions="selectClientOptions" @emitClientSubmit="emitClientSubmit" @emitClientClose="emitClientClose"></selectClient>
        </el-dialog>

        <!-- 借贷信息 -->
        <el-dialog title="借贷信息" :visible.sync="dialogDebitInfo" width="50%">
            <debitInfo :debitInfoOptions="debitInfoOptions" @emitDebitInfoSubmit="emitDebitInfoSubmit" @emitDebitInfoClose="emitDebitInfoClose"></debitInfo>
        </el-dialog>

        <!-- 卡折信息 -->
        <el-dialog title="卡折信息" :visible.sync="dialogCardInfo" width="50%">
            <el-form ref="formCard" :model="formCard"  label-width="80px">
                <el-form-item label="卡号">
                    <el-input v-model="formCard.bank_card_number" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="开户行">
                    <el-input v-model="formCard.open_bank" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="formCard.bank_card_pass" placeholder="请输入"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogCardInfo = false">取 消</el-button>
                <el-button type="primary" @click="handleSubmitCard()">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 操作记录 -->
        <el-dialog title="操作记录" :visible.sync="dialogHandleLogs" width="50%">
            <el-table :data="dataHandleLogs" border>
                <el-table-column prop="create_time" label="操作时间">
                </el-table-column>
                <el-table-column prop="user_name" label="操作人员">
                </el-table-column>
                <el-table-column prop="act_name" label="操作结果">
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogHandleLogs = false">关闭</el-button>
            </span>
        </el-dialog>
    
    </div>
</template>

<script>
    import * as API from '@/api/creditManagement'
    import selectClient from '@/components/selectClient'
    import debitInfo from '@/components/debitInfo'
    export default {
        data() {
            return {
                tableData: [],
                dataHandleLogs: [],
                page: 1,
                total: 1,
                limit: 10,
                hackReset: false,
                dialogHandleLogs: false,
                dialogCardInfo: false,
                dialogDebitInfo: false,
                dialogSelectClient: false,
                emitFormClien: {},
                formCard: {
                    bank_card_number: "",
                    bank_card_pass: "",
                    id: "",
                    open_bank: ""
                },
                selectClientOptions:{
                    formConfig: {
                        val: false
                    },
                    pageType: 'default',
                    isNextButton: true
                    
                },
                debitInfoOptions: {
                    pageType: 'help'
                },
                bankTime:[],
                ewTime:[],
                filters: {
                    contract_num:'',
                    contact_way: '',
                    borrower: '',
                    bank_loan_start: '',
                    bank_loan_end:'',
                    ew_loan_start:'',
                    ew_loan_end:'',
                    bank_withdraw:'',
                    bank_withdraw_end:'',
                    ew_withdraw_start:'',
                    ew_withdraw_end:'',
                    is_give:''
                }
            }
        },
        components:{
            selectClient,
            debitInfo
        },
        mounted() {
            this.getData();
        },
        computed: {
            bank_start(){
                return this.bankTime[0]
            },
            bank_end(){
                return this.bankTime[1]
            },
            ew_start(){
                return this.ewTime[0]
            },
            ew_end(){
                return this.ewTime[1]
            }
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    credit_type: 1,
                    page: this.page,
                    ...this.filters
                }
                API.creditHelpManagementList(params).then(res => {
                    this.tableData = res.data;
                    this.total = res.total;
                })
               
            },
            handleSearch() {
                this.getData();
            },
            resetFilters() {
                this.$refs.filters.resetFields();
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            handleAdd(){
                this.dialogSelectClient = true
                this.hackReset = false
                    this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            handleEdit(row){
                this.dialogCardInfo = true
                let params = {
                    broken_fold: row.broken_fold
                }

                API.creditHelpManagementView(params).then(res => {
                    Object.assign(this.formCard, res.data)
                    Object.assign(this.formCard, {
                        credit_id: row.id
                    })
                })
            },
            handleSubmitCard(){
                let params = {
                    id: this.formCard.id,
                    credit_id: this.formCard.credit_id,
                    bank_card_number: this.formCard.bank_card_number,
                    bank_card_pass : this.formCard.bank_card_pass,
                    open_bank : this.formCard.open_bank
                }
                API.creditHelpManagementEdit(params).then(res => {
                    this.dialogCardInfo = false
                    this.getData();
                })
            },
            handleSetGive(row,giveCode){
                let params = {
                    id: row.id,
                    is_give: giveCode
                }
                API.creditHelpManagementSetIsGive(params).then(res => {
                    this.getData();
                })
            },
            handleSetBlack(row){
                API.creditHelpManagementsetBlack({id: row.id}).then(res => {
                    this.getData();
                })
            },
            handleHandleLogs(row, index){
                this.dialogHandleLogs = true
                API.creditGetCreditActMsg({credit_id: row.id}).then(res => {
                    this.dataHandleLogs = res.data;
                })
            },
            emitClientSubmit(row){
                Object.assign(this.emitFormClien, row)
                Object.assign(this.debitInfoOptions, row)
                this.dialogSelectClient = false
                this.dialogDebitInfo = true
            },
            emitClientClose(){
                this.dialogSelectClient = false
            },
            emitDebitInfoSubmit(formdata){
                API.creditHelpManagementAdd(formdata).then(res => {
                    this.dialogDebitInfo = false
                    this.tableData = res.data;
                    this.getData();
                })
            },
            emitDebitInfoClose(){
                this.dialogDebitInfo = false
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
