<template>
	<div>
		<el-form ref="domFormDebitInfo" :model="formDebitInfo"  label-width="130px">
		    <el-form-item label=" ">
		        <el-radio-group v-model="formDebitInfo.is_card">
		            <el-radio-button label="1">新建卡折</el-radio-button>
		            <el-radio-button label="2">使用已有卡折</el-radio-button>
		        </el-radio-group>
		    </el-form-item>
		    <el-form-item label="卡号">
		        <el-input v-model="formDebitInfo.bank_card_number" placeholder="请输入" v-if="formDebitInfo.is_card == 1"></el-input>
		        <el-select v-model="formDebitInfo.bank_card_number" placeholder="请选择" v-else>
		            <el-option
		                v-for="item in optionsBankCard"
		                :key="item.bank_card_number"
		                :label="item.bank_card_number"
		                :value="item.bank_card_number">
		            </el-option>
		        </el-select>
		    </el-form-item>
		    <el-form-item label="开户行">
		        <el-input v-model="formDebitInfo.open_bank" placeholder="请输入"></el-input>
		    </el-form-item>
		    <el-form-item label="密码">
		        <el-input v-model="formDebitInfo.bank_card_pass" placeholder="请输入"></el-input>
		    </el-form-item>
		    <el-form-item label="银行贷款金额">
		        <el-input v-model="formDebitInfo.bank_loan_amount" placeholder="请输入"></el-input>
		    </el-form-item>
		    <el-form-item label="二网贷款金额">
		        <el-input v-model="formDebitInfo.ew_loan_amount" placeholder="请输入"></el-input>
		    </el-form-item>
		    <el-form-item label="总贷款金额">
		        <el-input v-model="formDebitInfo.total_loan_amount" placeholder="请输入"></el-input>
		    </el-form-item>
		    <el-form-item label="总贷款利率">
		        <el-input v-model="formDebitInfo.lending_rate" placeholder="请输入"></el-input>
		    </el-form-item>
		    <el-form-item label="银行放款时间">
		        <el-date-picker
					v-model="formDebitInfo.bank_loan_day"
					type="date"
					placeholder="选择日期">
				</el-date-picker>
		    </el-form-item>
		    <el-form-item label="二网公司放款时间">
		        <el-date-picker
					v-model="formDebitInfo.ew_loan_day"
					type="date"
					placeholder="选择日期">
				</el-date-picker>
		    </el-form-item>
		    <el-form-item label="贷款期数">
		        <el-select v-model="formDebitInfo.loan_staging" placeholder="请选择">
		            <el-option label="22" value="22"></el-option>
		            <el-option label="23" value="23"></el-option>
		            <el-option label="24" value="24"></el-option>
		        </el-select>
		    </el-form-item>
		    <el-form-item label="贷款合同号">
		        <el-input v-model="formDebitInfo.contract_num" placeholder="请输入"></el-input>
		    </el-form-item>
		</el-form>
		<div class="ta-r">
            <el-button @click="$emit('emitDebitInfoClose', false)">取 消</el-button>
            <el-button type="primary" @click="handleSubmit()">完 成</el-button>
        </div>
	</div>
    
</template>
<script>
import * as API from '@/api/creditManagement'
export default {
	props:{
        debitInfoOptions:{
            type: Object,
            default: {
                pageType: 'help'
            }
        }
    },
    data(){
        return {
            formDebitInfo: {
                is_card: '1',
                bank_card_number: '',
                open_bank: '',
                bank_card_pass: '',
                bank_loan_amount: '',
                ew_loan_amount: '',
                total_loan_amount: '',
                lending_rate: '',
                bank_loan_day: '',
                ew_loan_day: '',
                loan_staging: '',
                contract_num: ''
            }, 
            optionsBankCard: [{
                value: '1',
                label: '黄金糕'
            }, {
                value: '2',
                label: '双皮奶'
            }, {
                value: '3',
                label: '蚵仔煎'
            }, {
                value: '4',
                label: '龙须面'
            }, {
                value: '5',
                label: '北京烤鸭'
            }]
        }
    },
    mounted(){
    	this.getBankCard()
    },
    methods: {
        handleSubmit(){
        	let params = {
        		id_card: this.debitInfoOptions.id_card,
        		credit_type: this.debitInfoOptions.pageType == 'help' ? 1 : 2,
        		borrower: this.debitInfoOptions.name || this.debitInfoOptions.company_name,
        		address: this.debitInfoOptions.address,
        		contact_way: this.debitInfoOptions.mobile,
        		...this.formDebitInfo
        	}
            this.$emit('emitDebitInfoSubmit', params)
        },
        getBankCard(){
        	API.creditHelpManagementBankCard({id_card: this.debitInfoOptions.id_card}).then(res => {
                this.optionsBankCard = res.data;
            })
        }
    }
}
</script>
<style scoped>
</style>